#display {
  padding: .5em;
  margin: .5em;
  background-color: #525252;
}

#display h1 {
  font-weight: 500;
  color: white;
  font-size: 1rem;
  text-align: center;
}

.power {
  margin-top: 1em;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  
}

.checkbox-label {
  font-size: 1.3rem;
}

.switch {
  margin: .7em;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #c5961e;
}

input:focus + .slider {
  box-shadow: 0 0 1px #c5961e;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}